ween;\n  h1 {\n    color: $second-color;\n    margin: 0;\n    align-self: center;\n    span {\n      font-weight: 900;\n      text-decoration: underline;\n      color: $third-color;\n      text-shadow: 1px 1px 0px $text-light-color;\n    }\n  }\n  .hamburger {\n    display: block;\n    background: none;\n    border: none;\n    font-size: 2em;\n    font-weight: 900;\n    color: $text-light-color;\n    cursor: pointer;\n  }\n  #navigation {\n    background-color: $second-color;\n    position: absolute;\n    display: none;\n    width: 120px;\n    height: 200px;\n    padding-top: 20px;\n    ul {\n      display: flex;\n      flex-direction: column;\n      gap: 0.5rem;\n      width: 100%;\n      padding: 0 10px;\n      li {\n        list-style-type: none;\n        font-size: 1em;\n        font-weight: 900;\n\n        a {\n          display: block;\n          text-decoration: none;\n          color: black;\n          padding: 0 10px;\n          align-content: center;\n        }\n      }\n    }\n  }\n  #navigation.open {\n    top: 0;\n    right: 0;\n    display: flex;\n  }\n}\n\n.hero {\n  .image-hero {\n    // position: absolute;\n    // z-index: 0;\n    img,\n    source {\n      object-fit: cover;\n      object-position: center;\n      width: 100%;\n      height: 500px;\n      min-height: 250px;\n    }\n  }\n  display: flex;\n  flex-direction: column;\n  // background-image: url('../public/images/hero-image_1.jpg');\n  // background-position: center;\n  // background-size: cover;\n  justify-content: center;\n  // align-items: center;\n  p {\n    position: absolute;\n    align-self: center;\n    padding: 2rem;\n    font-size: 2.5em;\n    text-shadow: 1px 2px 1px $text-dark-color;\n    color: $text-light-color;\n  }\n}\n\nmain {\n  padding: 1rem;\n  display: flex;\n  flex-direction: column;\n  max-width: 100%;\n  h1 {\n    text-align: center;\n    font-size: 2em;\n    color: $text-dark-color;\n  }\n  .paragraph {\n    text-indent: 3em;\n    padding: 0 15px;\n    text-align: